@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* https://fonts.google.com/specimen/IBM+Plex+Mono?query=ibm+pl */
:root {
  --primary: #e03372;
  --light: #ffffff;
  --dark: #121212;

  --font-mono: 'IBM Plex Mono', 'Courier New', Courier, monospace;

  --font-very-small: 0.6rem;
  --font-small: 0.8rem;
  --font-h6: 1rem;
  --font-h5: 1.333rem;
  --font-h4: 1.777rem;
  --font-h3: 2.369rem;
  --font-h2: 3.157rem;
  --font-h1: 4.209rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'IBM Plex Mono', 'Courier New', Courier, monospace;
  font-family: var(--font-mono);
  background-color: #121212;
  background-color: var(--dark);
  color: #ffffff;
  color: var(--light);
}
h1 {
  font-size: 4.209rem;
  font-size: var(--font-h1);
}
h2 {
  font-size: 3.157rem;
  font-size: var(--font-h2);
}
h3 {
  font-size: 2.369rem;
  font-size: var(--font-h3);
  font-weight: 400;
}
h4 {
  font-size: 1.777rem;
  font-size: var(--font-h4);
  font-weight: 600;
}
h5 {
  font-weight: 600;
}

p {
  font-size: 0.8rem;
  font-size: var(--font-small);
  line-height: 1.3;
}

p,
button,
input {
  letter-spacing: 0.24px;
}

span {
  font-size: 0.8rem;
  font-size: var(--font-small);
  font-weight: 500;
}

img {
  display: block;
  max-width: 100%;
}

input {
  padding: 13px 11px 12px 11px;
  border: 2px solid;
  color: #e03372;
  color: var(--primary);
  min-width: 200px;
}

input:focus,
input:active {
  outline: none;
  border: 2px dashed;
}
/* ----------------------- helpers ----------------------- */
.flex {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
/* not in use */
.flex-start {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.flex-center-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.flex-space-between {
  justify-content: space-between;
}
.none {
  display: none;
}

.gap-10 {
  grid-gap: 1rem;
  gap: 1rem;
}

.gap-20 {
  grid-gap: 2rem;
  gap: 2rem;
}

.icon-bg-light path {
  fill: #ffffff;
  fill: var(--light);
}
/* ----------------------- button ----------------------- */
.btn {
  padding: 10px;
  outline: none;
  border: 2px solid transparent;
  border-radius: 0;
  min-width: 200px;
  font-family: 'IBM Plex Mono', 'Courier New', Courier, monospace;
  font-family: var(--font-mono);
  font-weight: 500;
  font-size: 1rem;
  font-size: var(--font-h6);
  /* icon */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn-small {
  font-size: 0.8rem;
  font-size: var(--font-small);
}
.btn-big {
  font-size: 1.777rem;
  font-size: var(--font-h4);
}
.btn:hover {
  cursor: pointer;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}
.btn:active {
  border: 2px dashed;
}
.btn-primary {
  background-color: #e03372;
  background-color: var(--primary);
  color: #ffffff;
  color: var(--light);
}

.btn-primary svg path,
.btn-primary svg {
  fill: #ffffff;
  fill: var(--light);
}
.btn-secondary {
  background-color: #ffffff;
  background-color: var(--light);
  color: #e03372;
  color: var(--primary);
}

.btn-secondary svg path,
.btn-secondary svg {
  fill: #e03372;
  fill: var(--primary);
}

.btn-icon {
  border: none;
  outline: none;
  background: #e03372;
  background: var(--primary);
  color: #ffffff;
  color: var(--light);

  border-radius: 30px;

  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-icon svg {
  height: 18px;
  width: 18px;
}

.btn-icon:hover {
  cursor: pointer;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}
/* ----------------------- input ----------------------- */
.field {
  display: flex;
  flex-direction: column;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  margin-bottom: 0.75rem;
}
.field label {
  font-size: 0.6rem;
  font-size: var(--font-very-small);
  font-weight: 400;
  letter-spacing: 0.5px;
}
/* ----------------------- card ----------------------- */
.card {
  background: #ffffff;
  background: var(--light);
  color: #121212;
  color: var(--dark);
  border: 3px solid #e03372;
  border: 3px solid var(--primary);
  width: 200px;
}

.card-poster {
  position: relative;
}

.card-body {
  padding: 10px;
}

.card-body > div {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card-body > div:last-child {
  margin-bottom: 0;
}

.card-item,
.card-price {
  display: flex;
  align-items: center;
}

.card-price {
  position: absolute;
  top: -12px;
  right: -12px;
  background: #ffffff;
  background: var(--light);
  padding: 4px;
  border: 2px solid;
}
/* ----------------------- dice ----------------------- */
.dice-active path {
  fill: #e03372;
  fill: var(--primary);
}
/* ----------------------- modal ----------------------- */
.modal-none {
  display: none;
}
.modal-show {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.85);
}

.modal-container {
  min-width: 500px;
  background: #ffffff;
  background: var(--light);
  color: #121212;
  color: var(--dark);
}

.modal-container h4 {
  text-align: center;
  padding: 1rem 0 2rem;
}

.modal-container-body {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}
/* ----------------------- page ----------------------- */
.page {
  background: #ffffff;
  background: var(--light);
  color: #121212;
  color: var(--dark);
  padding: 2rem;
  min-width: 700px;
  min-height: 400px;
}
.page-container {
  margin-bottom: 22px;
}
.page-buttons {
  position: absolute;
  /* top: 50%; */
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
}
.page-buttons > div {
  display: flex;
}
.page h4 {
  margin-bottom: 2rem;
}
/* ----------------------- position ----------------------- */
.positions-container {
  background-color: #f1f1f1;
  border: 2px solid #333;
  padding: 4px;
  max-height: 400px;
  overflow-y: scroll;
  margin-left: 2rem;
  margin-right: 2rem;
}
.positions {
  display: flex;
  grid-gap: 3px;
  gap: 3px;
  flex-wrap: wrap;
  justify-content: center;
}
.po {
  height: 130px;
  width: 130px;

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-size: var(--font-h6);
  border: 2px solid;
}
.po path {
  stroke: #121212;
  stroke: var(--dark);
  stroke-width: 2px;
}
.none {
  background: #ffffff;
  background: var(--light);
  color: #121212;
  color: var(--dark);
}
.init {
  background-color: #121212;
  background-color: var(--dark);
  color: #e03372;
  color: var(--primary);
}
.fight {
  background-color: #ffffff;
  background-color: var(--light);
  color: #121212;
  color: var(--dark);
}
.item {
  background-color: #ffffff;
  background-color: var(--light);
  color: #121212;
  color: var(--dark);
}
.boss {
  background-color: #121212;
  background-color: var(--dark);
  color: #e03372;
  color: var(--primary);
}
/* ----------------------- list ----------------------- */
.list {
  width: 100%;
}
.list-item {
  display: grid;
  grid-template-columns: 1fr repeat(6, auto);
  align-items: center;

  border: 1px solid #343434;
  padding: 4px;
  margin-bottom: 4px;
}
.list-item > span {
  border-right: 1px solid #343434;
  padding: 0 10px;

  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  line-height: 1.2;
}
/* ----------------------- scroll ----------------------- */
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #333;
}
::-webkit-scrollbar-thumb {
  background: #6b6b6b;
}
::-webkit-scrollbar-thumb:hover {
  background: #e03372;
  background: var(--primary);
}
/* ----------------------- scroll ----------------------- */
.blink {
  -webkit-animation: blink-animation 3s infinite;
          animation: blink-animation 3s infinite;
}

.dash {
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 10s linear forwards;
          animation: dash 10s linear forwards;
}

@-webkit-keyframes blink-animation {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes blink-animation {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}

a {
  text-decoration: none;
  color: var(--primary);
}
a:hover {
  text-decoration: underline;
}

ul li {
  display: inline-block;
  padding: 10px;
}

